import React, { useState, useEffect, useRef } from "react";

import Card from "../UI/Card";
import useHttp from "../../hooks/use-http";
import ErrorModal from "../UI/ErrorModal";
import "./Search.css";

const Search = React.memo((props) => {
  const [enteredFilter, setEnteredFilter] = useState("");
  const { onLoadIngredients } = props;
  const inputRef = useRef();
  const { data, error, clear, isLoading, sendRequest } = useHttp();
  useEffect(() => {
    const timer = setTimeout(() => {
      if (enteredFilter === inputRef.current.value) {
        const query =
          enteredFilter.length === 0
            ? ""
            : `?orderBy="title"&startAt="${enteredFilter}"&endAt="${enteredFilter}\uf8ff"`;

        sendRequest(
          "https://react-http-9bce5-default-rtdb.firebaseio.com/ingredients.json" +
            query,
          "GET"
        );
      }
    }, 500);

    return () => {
      clearTimeout(timer);
    };
  }, [onLoadIngredients, enteredFilter, inputRef]);
  useEffect(() => {
    if (data && !error && !isLoading) {
      const filteredData = [];
      for (const key in data) {
        filteredData.push({
          id: key,
          title: data[key].title,
          amount: data[key].amount,
        });
      }

      onLoadIngredients(filteredData);
    }
  }, [data, error, isLoading]);
  return (
    <section className="search">
      {error && <ErrorModal onClose={clear}>{error}</ErrorModal>}
      <Card>
        <div className="search-input">
          <label>Filter by Title</label>
          {isLoading && "Loading..."}
          <input
            type="text"
            ref={inputRef}
            value={enteredFilter}
            onChange={(e) => setEnteredFilter(e.target.value)}
          />
        </div>
      </Card>
    </section>
  );
});

export default Search;
