import React, {
  useState,
  useReducer,
  useCallback,
  useEffect,
  useMemo,
} from "react";
import IngredientList from "./IngredientList";
import IngredientForm from "./IngredientForm";
import Search from "./Search";
import ErrorModal from "../UI/ErrorModal";
import useHttp from "../../hooks/use-http";
const ingredientReducer = (currentIngredients, action) => {
  switch (action.type) {
    case "SET":
      return action.ingredients;
    case "ADD":
      return [...currentIngredients, action.ingredient];
    case "DELETE":
      return currentIngredients.filter((ing) => ing.id !== action.id);
    default:
      throw new Error("Should not get there!");
  }
};

function Ingredients() {
  const [ingredientsState, dispatch] = useReducer(ingredientReducer, []);
  // const [httpState, dispatchHttp] = useReducer(httpReducer, {
  //   loading: false,
  //   error: null,
  // });
  const {
    isLoading,
    data,
    sendRequest,
    error,
    reqExtra,
    reqIdentifier,
    clear,
  } = useHttp();
  // const [ingredients, setIngredients] = useState([]);
  // const [isLoading, setIsLoading] = useState(false);
  // const [error, setError] = useState();

  const filteredIngredientsHandler = useCallback((filteredIngredients) => {
    // setIngredients(filteredIngredients);
    dispatch({ type: "SET", ingredients: filteredIngredients });
  }, []);
  useEffect(() => {
    if (!isLoading && !error && reqIdentifier === "REMOVE_INGREDIENT") {
      dispatch({ type: "DELETE", id: reqExtra });
    } else if (reqIdentifier === "ADD_INGREDIENT" && !isLoading && !error) {
      dispatch({
        type: "ADD",
        ingredient: { id: data.name, ...reqExtra },
      });
    }
  }, [data, reqExtra, reqIdentifier]);
  const addIngredient = useCallback(
    (ingredient) => {
      sendRequest(
        "https://react-http-9bce5-default-rtdb.firebaseio.com/ingredients.json",
        "POST",
        JSON.stringify(ingredient),
        ingredient,
        "ADD_INGREDIENT"
      );
    },
    [sendRequest]
  );
  const removeIgredient = (id) => {
    // setIsLoading(true);
    // dispatchHttp({ type: "SEND" });
    // fetch(
    //   `https://react-http-9bce5-default-rtdb.firebaseio.com/ingredients/${id}.json`,
    //   {
    //     method: "DELETE",
    //   }
    // )
    //   .then(() => {
    //     // setIsLoading(false);

    //     // setIngredients((prevIngre) =>
    //     //   prevIngre.filter((item) => item.id !== id)
    //     // );
    //     dispatch({ type: "DELETE", id: id });
    //     dispatchHttp({ type: "RESPONSE" });
    //   })
    //   .catch((e) => {
    //     // setError("Something Went Wrong: " + e.message);
    //     // setIsLoading(false);
    //     console.log(e);
    //     dispatchHttp({
    //       type: "ERROR",
    //       error: "Something Went Wrong: " + e.message,
    //     });
    //     console.log(httpState);
    //   });
    sendRequest(
      `https://react-http-9bce5-default-rtdb.firebaseio.com/ingredients/${id}.json`,
      "DELETE",
      null,
      id,
      "REMOVE_INGREDIENT"
    );
  };
  const clearError = () => {
    clear();
  };
  const ingredientList = useMemo(() => {
    return (
      <IngredientList
        ingredients={ingredientsState}
        onRemoveItem={removeIgredient}
      />
    );
  }, [ingredientsState, removeIgredient]);
  return (
    <div className="App">
      {error && <ErrorModal onClose={clearError}>{error}</ErrorModal>}
      <IngredientForm onAddIngredient={addIngredient} loading={isLoading} />

      <section>
        <Search onLoadIngredients={filteredIngredientsHandler} />
        {ingredientList}
      </section>
    </div>
  );
}

export default Ingredients;
